import {AUDIO_ENC_CODE, AVC_PACKET_TYPE} from "../constant";
import ExpGolomb from "./exp-golomb";

/**
 *
 * @param arrayItem
 * @returns {Uint8Array}
 */
export function aacEncoderConfigurationRecord(arrayItem) {
    // 7ADTS
    const accADTSHeader = arrayItem.slice(0, 7);
    // adts
    //     aac 
    // 
    // asc 
    // 0  1 
    // 0: Main profile
    // 1: Low Complexity profile(LC)
    // 2: Scalable Sampling Rate profile(SSR)
    const profile = ((accADTSHeader[2] & 0xc0) >> 6) + 1;
    // const profile = 0;
    // const sampleRate = (+audioInfo.sampleRate);
    const sampleRate = ((accADTSHeader[2] & 0x3C) >> 2)
    // const channel = (+audioInfo.channel);
    const channel = ((accADTSHeader[2] & 0x1) << 2) | ((accADTSHeader[3] & 0xc0) >> 6)
    const config1 = (profile << 3) | ((sampleRate & 0xe) >> 1)
    const config2 = ((sampleRate & 0x1) << 7) | (channel << 3)
    console.log('aacEncoderConfigurationRecord', `profile: ${profile}`, `sampleRate: ${sampleRate}`, `channel: ${channel}`)
    // 0xAF >> 4 === 10
    const temp = [0xAF, 0x00, config1, config2];
    const arrayBuffer = new Uint8Array(temp);

    return arrayBuffer;
}

/**
 * aacasc
 * @param profile
 * @param sampleRate  index, 
 * @param channel
 * @returns {Uint8Array}
 */
export function aacEncoderConfigurationRecordV2({profile, sampleRate, channel}) {
    const config1 = (profile << 3) | ((sampleRate & 0xe) >> 1)
    const config2 = ((sampleRate & 0x1) << 7) | (channel << 3)
    // 0xAF >> 4 === 10
    const temp = [0xAF, 0x00, config1, config2];
    const arrayBuffer = new Uint8Array(temp);

    return arrayBuffer;
}

/**
 *
 * @param arrayItem
 * @returns {Uint8Array}
 */
export function aacEncoderNalePacket(arrayItem) {
    arrayItem.unshift(0xAF, 0x01);
    const arrayBuffer = new Uint8Array(arrayItem);
    return arrayBuffer;
}

/**
 *
 * @param payload
 * @returns {boolean}
 */
export function isAacCodecPacket(payload) {
    return isAAC(payload) && payload[1] === AVC_PACKET_TYPE.sequenceHeader
}

export function isAAC(payload) {
    return (payload[0] >> 4) === AUDIO_ENC_CODE.AAC;
}

const FREQ = [
    96000,
    88200,
    64000,
    48000,
    44100,
    32000,
    24000,
    22050,
    16000,
    12000,
    11025,
    8000,
    7350
];
const MPEG4SamplingFrequencies = FREQ;


export const AAC_FREQ_LIST = FREQ;

export function getSilentFrame(codec, channelCount) {
    switch (codec) {
        case 'mp4a.40.2':
            if (channelCount === 1) {
                return new Uint8Array([0x00, 0xc8, 0x00, 0x80, 0x23, 0x80])
            }
            if (channelCount === 2) {
                return new Uint8Array([
                    0x21, 0x00, 0x49, 0x90, 0x02, 0x19, 0x00, 0x23, 0x80
                ])
            }
            if (channelCount === 3) {
                return new Uint8Array([
                    0x00, 0xc8, 0x00, 0x80, 0x20, 0x84, 0x01, 0x26, 0x40, 0x08, 0x64,
                    0x00, 0x8e
                ])
            }
            if (channelCount === 4) {
                return new Uint8Array([
                    0x00, 0xc8, 0x00, 0x80, 0x20, 0x84, 0x01, 0x26, 0x40, 0x08, 0x64,
                    0x00, 0x80, 0x2c, 0x80, 0x08, 0x02, 0x38
                ])
            }
            if (channelCount === 5) {
                return new Uint8Array([
                    0x00, 0xc8, 0x00, 0x80, 0x20, 0x84, 0x01, 0x26, 0x40, 0x08, 0x64,
                    0x00, 0x82, 0x30, 0x04, 0x99, 0x00, 0x21, 0x90, 0x02, 0x38
                ])
            }
            if (channelCount === 6) {
                return new Uint8Array([
                    0x00, 0xc8, 0x00, 0x80, 0x20, 0x84, 0x01, 0x26, 0x40, 0x08, 0x64,
                    0x00, 0x82, 0x30, 0x04, 0x99, 0x00, 0x21, 0x90, 0x02, 0x00, 0xb2,
                    0x00, 0x20, 0x08, 0xe0
                ])
            }
            break
        default:
            if (channelCount === 1) {
                return new Uint8Array([
                    0x1, 0x40, 0x22, 0x80, 0xa3, 0x4e, 0xe6, 0x80, 0xba, 0x8, 0x0, 0x0,
                    0x0, 0x1c, 0x6, 0xf1, 0xc1, 0xa, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a,
                    0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a,
                    0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a,
                    0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a,
                    0x5a, 0x5e
                ])
            }
            if (channelCount === 2) {
                return new Uint8Array([
                    0x1, 0x40, 0x22, 0x80, 0xa3, 0x5e, 0xe6, 0x80, 0xba, 0x8, 0x0, 0x0,
                    0x0, 0x0, 0x95, 0x0, 0x6, 0xf1, 0xa1, 0xa, 0x5a, 0x5a, 0x5a, 0x5a,
                    0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a,
                    0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a,
                    0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a,
                    0x5a, 0x5e
                ])
            }
            if (channelCount === 3) {
                return new Uint8Array([
                    0x1, 0x40, 0x22, 0x80, 0xa3, 0x5e, 0xe6, 0x80, 0xba, 0x8, 0x0, 0x0,
                    0x0, 0x0, 0x95, 0x0, 0x6, 0xf1, 0xa1, 0xa, 0x5a, 0x5a, 0x5a, 0x5a,
                    0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a,
                    0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a,
                    0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a, 0x5a,
                    0x5a, 0x5e
                ])
            }
            break
    }
}

export function getFrameDuration(rate, timescale = 90000) {
    return 1024 * timescale / rate
}

export function parseADTS(data, pts) {
    const len = data.length
    let i = 0

    while ((i + 2) < len) {
        if (data[i] === 0xff && (data[i + 1] & 0xf6) === 0xf0) {
            break
        }
        i++
    }

    if (i >= len) return

    const skip = i
    const frames = []
    const samplingFrequencyIndex = (data[i + 2] & 0x3c) >>> 2
    const sampleRate = FREQ[samplingFrequencyIndex]
    if (!sampleRate) throw new Error(`Invalid sampling index: ${samplingFrequencyIndex}`)
    //  profile
    const objectType = ((data[i + 2] & 0xc0) >>> 6) + 1
    //  channel
    const channelCount = ((data[i + 2] & 1) << 2) | ((data[i + 3] & 0xc0) >>> 6)

    let protectionSkipBytes
    let frameLength
    let frameIndex = 0
    const duration = getFrameDuration(sampleRate)

    while ((i + 7) < len) {
        if ((data[i] !== 0xff) || (data[i + 1] & 0xF6) !== 0xf0) {
            i++
            continue
        }

        frameLength = ((data[i + 3] & 0x03) << 11) | (data[i + 4] << 3) | ((data[i + 5] & 0xe0) >> 5)
        if ((len - i) < frameLength) break

        protectionSkipBytes = (~data[i + 1] & 0x01) * 2
        frames.push({
            pts: pts + frameIndex * duration,
            data: data.subarray(i + 7 + protectionSkipBytes, i + frameLength),
        })

        frameIndex++
        i += frameLength
    }

    return {
        skip,
        remaining: i >= len ? undefined : data.subarray(i),
        frames,
        samplingFrequencyIndex,
        sampleRate,
        objectType,
        channelCount,
        originCodec: `mp4a.40.${objectType}`
    }
}


const _mpegSamplingRates = [
    96000, 88200, 64000, 48000, 44100, 32000,
    24000, 22050, 16000, 12000, 11025, 8000, 7350
];

export function parseAACAudioSpecificConfig(arrayBuffer) {
    let array = new Uint8Array(arrayBuffer);
    let config = null;

    /* Audio Object Type:
       0: Null
       1: AAC Main
       2: AAC LC
       3: AAC SSR (Scalable Sample Rate)
       4: AAC LTP (Long Term Prediction)
       5: HE-AAC / SBR (Spectral Band Replication)
       6: AAC Scalable
    */

    let audioObjectType = 0;
    let originalAudioObjectType = 0;
    let audioExtensionObjectType = null;
    let samplingIndex = 0;
    let extensionSamplingIndex = null;

    // 5 bits
    audioObjectType = originalAudioObjectType = array[0] >>> 3;
    // 4 bits
    samplingIndex = ((array[0] & 0x07) << 1) | (array[1] >>> 7);
    if (samplingIndex < 0 || samplingIndex >= _mpegSamplingRates.length) {
        console.error('Flv: AAC invalid sampling frequency index!')
        return;
    }

    let samplingFrequence = _mpegSamplingRates[samplingIndex];

    // 4 bits
    let channelConfig = (array[1] & 0x78) >>> 3;
    if (channelConfig < 0 || channelConfig >= 8) {
        console.log('Flv: AAC invalid channel configuration')
        return;
    }

    if (audioObjectType === 5) {  // HE-AAC?
        // 4 bits
        extensionSamplingIndex = ((array[1] & 0x07) << 1) | (array[2] >>> 7);
        // 5 bits
        audioExtensionObjectType = (array[2] & 0x7C) >>> 2;
    }

    // workarounds for various browsers
    let userAgent = self.navigator.userAgent.toLowerCase();

    if (userAgent.indexOf('firefox') !== -1) {
        // firefox: use SBR (HE-AAC) if freq less than 24kHz
        if (samplingIndex >= 6) {
            audioObjectType = 5;
            config = new Array(4);
            extensionSamplingIndex = samplingIndex - 3;
        } else {  // use LC-AAC
            audioObjectType = 2;
            config = new Array(2);
            extensionSamplingIndex = samplingIndex;
        }
    } else if (userAgent.indexOf('android') !== -1) {
        // android: always use LC-AAC
        audioObjectType = 2;
        config = new Array(2);
        extensionSamplingIndex = samplingIndex;
    } else {
        // for other browsers, e.g. chrome...
        // Always use HE-AAC to make it easier to switch aac codec profile
        audioObjectType = 5;
        extensionSamplingIndex = samplingIndex;
        config = new Array(4);

        if (samplingIndex >= 6) {
            extensionSamplingIndex = samplingIndex - 3;
        } else if (channelConfig === 1) {  // Mono channel
            audioObjectType = 2;
            config = new Array(2);
            extensionSamplingIndex = samplingIndex;
        }
    }

    config[0] = audioObjectType << 3;
    config[0] |= (samplingIndex & 0x0F) >>> 1;
    config[1] = (samplingIndex & 0x0F) << 7;
    config[1] |= (channelConfig & 0x0F) << 3;
    if (audioObjectType === 5) {
        config[1] |= ((extensionSamplingIndex & 0x0F) >>> 1);
        config[2] = (extensionSamplingIndex & 0x01) << 7;
        // extended audio object type: force to 2 (LC-AAC)
        config[2] |= (2 << 2);
        config[3] = 0;
    }

    return {
        audioType: 'aac',
        config: config,
        sampleRate: samplingFrequence,
        channelCount: channelConfig,
        objectType: audioObjectType,
        codec: 'mp4a.40.' + audioObjectType,
        originalCodec: 'mp4a.40.' + originalAudioObjectType
    };
}

class Bitop {

    constructor(buffer) {
        this.buffer = buffer;
        this.buflen = buffer.length;
        this.bufpos = 0;
        this.bufoff = 0;
        this.iserro = false;
    }

    read(n) {
        let v = 0;
        let d = 0;
        while (n) {
            if (n < 0 || this.bufpos >= this.buflen) {
                this.iserro = true;
                return 0;
            }

            this.iserro = false;
            d = this.bufoff + n > 8 ? 8 - this.bufoff : n;

            v <<= d;
            v += (this.buffer[this.bufpos] >> (8 - this.bufoff - d)) & (0xff >> (8 - d));

            this.bufoff += d;
            n -= d;

            if (this.bufoff == 8) {
                this.bufpos++;
                this.bufoff = 0;
            }
        }
        return v;
    }

    look(n) {
        let p = this.bufpos;
        let o = this.bufoff;
        let v = this.read(n);
        this.bufpos = p;
        this.bufoff = o;
        return v;
    }

    read_golomb() {
        let n;
        for (n = 0; this.read(1) == 0 && !this.iserro; n++) ;
        return (1 << n) + this.read(n) - 1;
    }
}

function getObjectType(bitop) {
    let audioObjectType = bitop.read(5);
    if (audioObjectType === 31) {
        audioObjectType = bitop.read(6) + 32;
    }
    return audioObjectType;
}

function getSampleRate(bitop, info) {
    info.sampling_index = bitop.read(4);
    return info.sampling_index == 0x0f ? bitop.read(24) : AAC_SAMPLE_RATE[info.sampling_index];
}

const AAC_SAMPLE_RATE = [
    96000, 88200, 64000, 48000,
    44100, 32000, 24000, 22050,
    16000, 12000, 11025, 8000,
    7350, 0, 0, 0
];

const AAC_CHANNELS = [
    0, 1, 2, 3, 4, 5, 6, 8
];

export function readAACSpecificConfig(aacSequenceHeader) {
    let info = {};
    let bitop = new Bitop(aacSequenceHeader);
    bitop.read(16);
    info.object_type = getObjectType(bitop);
    info.sample_rate = getSampleRate(bitop, info);
    info.chan_config = bitop.read(4);
    if (info.chan_config < AAC_CHANNELS.length) {
        info.channels = AAC_CHANNELS[info.chan_config];
    }
    info.sbr = -1;
    info.ps = -1;
    if (info.object_type == 5 || info.object_type == 29) {
        if (info.object_type == 29) {
            info.ps = 1;
        }
        info.ext_object_type = 5;
        info.sbr = 1;
        info.sample_rate = getSampleRate(bitop, info);
        info.object_type = getObjectType(bitop);
    }

    return {
        ...info,
        channelCount: info.channels,
        sampleRate: info.sample_rate,
    };
}

export class AACADTSParser {
    constructor(data) {
        this.data_ = data;
        this.eof_flag_ = false;
        this.current_syncword_offset_ = this.findNextSyncwordOffset(0);
        if (this.eof_flag_) {
            console.error('Could not found ADTS syncword until payload end');
        }
    }

    findNextSyncwordOffset(syncword_offset) {
        let i = syncword_offset;
        let data = this.data_;

        while (true) {
            if (i + 7 >= data.byteLength) {
                this.eof_flag_ = true;
                return data.byteLength;
            }

            // search 12-bit 0xFFF syncword
            let syncword = ((data[i + 0] << 8) | data[i + 1]) >>> 4;
            if (syncword === 0xFFF) {
                return i;
            } else {
                i++;
            }
        }
    }

    readNextAACFrame() {
        let data = this.data_;
        let aac_frame = null;

        while (aac_frame == null) {
            if (this.eof_flag_) {
                break;
            }

            let syncword_offset = this.current_syncword_offset_;
            let offset = syncword_offset;

            // adts_fixed_header()
            // syncword 0xFFF: 12-bit
            let ID = (data[offset + 1] & 0x08) >>> 3;
            let layer = (data[offset + 1] & 0x06) >>> 1;
            let protection_absent = data[offset + 1] & 0x01;
            let profile = (data[offset + 2] & 0xC0) >>> 6;
            let sampling_frequency_index = (data[offset + 2] & 0x3C) >>> 2;
            let channel_configuration = ((data[offset + 2] & 0x01) << 2)
                | ((data[offset + 3] & 0xC0) >>> 6);

            // adts_variable_header()
            let aac_frame_length = ((data[offset + 3] & 0x03) << 11)
                | (data[offset + 4] << 3)
                | ((data[offset + 5] & 0xE0) >>> 5);
            let number_of_raw_data_blocks_in_frame = data[offset + 6] & 0x03;

            if (offset + aac_frame_length > this.data_.byteLength) {
                // data not enough for extracting last sample
                this.eof_flag_ = true;
                this.has_last_incomplete_data = true;
                break;
            }

            let adts_header_length = (protection_absent === 1) ? 7 : 9;
            let adts_frame_payload_length = aac_frame_length - adts_header_length;

            offset += adts_header_length;

            let next_syncword_offset = this.findNextSyncwordOffset(offset + adts_frame_payload_length);
            this.current_syncword_offset_ = next_syncword_offset;

            if ((ID !== 0 && ID !== 1) || layer !== 0) {
                // invalid adts frame ?
                continue;
            }

            let frame_data = data.subarray(offset, offset + adts_frame_payload_length);

            aac_frame = {};
            aac_frame.audio_object_type = (profile + 1);
            aac_frame.sampling_freq_index = sampling_frequency_index;
            aac_frame.sampling_frequency = MPEG4SamplingFrequencies[sampling_frequency_index];
            aac_frame.channel_config = channel_configuration;
            aac_frame.data = frame_data;
        }

        return aac_frame;
    }

    hasIncompleteData() {
        return this.has_last_incomplete_data;
    }

    getIncompleteData() {
        if (!this.has_last_incomplete_data) {
            return null;
        }

        return this.data_.subarray(this.current_syncword_offset_);
    }
}


export class AACLOASParser {
    constructor(data) {
        this.data_ = data;
        this.eof_flag_ = false;
        this.current_syncword_offset_ = this.findNextSyncwordOffset(0);
        if (this.eof_flag_) {
            console.error('Could not found ADTS syncword until payload end');
        }
    }

    findNextSyncwordOffset(syncword_offset) {
        let i = syncword_offset;
        let data = this.data_;

        while (true) {
            if (i + 1 >= data.byteLength) {
                this.eof_flag_ = true;
                return data.byteLength;
            }

            // search 12-bit 0xFFF syncword
            let syncword = (data[i + 0] << 3) | (data[i + 1] >>> 5);
            if (syncword === 0x2B7) {
                return i;
            } else {
                i++;
            }
        }
    }

    getLATMValue(gb) {
        let bytesForValue = gb.readBits(2);
        let value = 0;
        for (let i = 0; i <= bytesForValue; i++) {
            value = value << 8;
            value = value | gb.readByte();
        }
        return value;
    }

    readNextAACFrame(privious) {
        let data = this.data_;
        let aac_frame = null;

        while (aac_frame == null) {
            if (this.eof_flag_) {
                break;
            }

            let syncword_offset = this.current_syncword_offset_;
            let offset = syncword_offset;

            let audioMuxLengthBytes = ((data[offset + 1] & 0x1F) << 8) | data[offset + 2];
            if (offset + 3 + audioMuxLengthBytes >= this.data_.byteLength) {
                // data not enough for extracting last sample
                this.eof_flag_ = true;
                this.has_last_incomplete_data = true;
                break;
            }

            // AudioMuxElement(1)
            let gb = new ExpGolomb(data.subarray(offset + 3, offset + 3 + audioMuxLengthBytes));
            let useSameStreamMux = gb.readBool();
            let streamMuxConfig = null;
            if (!useSameStreamMux) {
                let audioMuxVersion = gb.readBool();
                let audioMuxVersionA = audioMuxVersion && gb.readBool();
                if (audioMuxVersionA) {
                    console.error('audioMuxVersionA is Not Supported');
                    gb.destroy();
                    break;
                }
                if (audioMuxVersion) {
                    this.getLATMValue(gb);
                }
                let allStreamsSameTimeFraming = gb.readBool();
                if (!allStreamsSameTimeFraming) {
                    console.error('allStreamsSameTimeFraming zero is Not Supported')
                    gb.destroy();
                    break;
                }
                let numSubFrames = gb.readBits(6);
                if (numSubFrames !== 0) {
                    console.error('more than 2 numSubFrames Not Supported')
                    gb.destroy();
                    break;
                }
                let numProgram = gb.readBits(4);
                if (numProgram !== 0) {
                    console.error('more than 2 numProgram Not Supported')
                    gb.destroy();
                    break;
                }
                let numLayer = gb.readBits(3);
                if (numLayer !== 0) {
                    console.error('more than 2 numLayer Not Supported')
                    gb.destroy();
                    break;
                }

                let fillBits = audioMuxVersion ? this.getLATMValue(gb) : 0;
                let audio_object_type = gb.readBits(5);
                fillBits -= 5;
                let sampling_freq_index = gb.readBits(4);
                fillBits -= 4;
                let channel_config = gb.readBits(4);
                fillBits -= 4;
                gb.readBits(3);
                fillBits -= 3; // GA Specfic Config
                if (fillBits > 0) {
                    gb.readBits(fillBits);
                }

                let frameLengthType = gb.readBits(3);
                if (frameLengthType === 0) {
                    gb.readByte();
                } else {
                    console.error(`frameLengthType = ${frameLengthType}. Only frameLengthType = 0 Supported`)
                    gb.destroy();
                    break;
                }

                let otherDataPresent = gb.readBool();
                if (otherDataPresent) {
                    if (audioMuxVersion) {
                        this.getLATMValue(gb);
                    } else {
                        let otherDataLenBits = 0;
                        while (true) {
                            otherDataLenBits = otherDataLenBits << 8;
                            let otherDataLenEsc = gb.readBool();
                            let otherDataLenTmp = gb.readByte();
                            otherDataLenBits += otherDataLenTmp
                            if (!otherDataLenEsc) {
                                break;
                            }
                        }
                        console.log(otherDataLenBits)
                    }
                }

                let crcCheckPresent = gb.readBool();
                if (crcCheckPresent) {
                    gb.readByte();
                }

                streamMuxConfig = {};
                streamMuxConfig.audio_object_type = audio_object_type;
                streamMuxConfig.sampling_freq_index = sampling_freq_index;
                streamMuxConfig.sampling_frequency = MPEG4SamplingFrequencies[streamMuxConfig.sampling_freq_index];
                streamMuxConfig.channel_config = channel_config;
                streamMuxConfig.other_data_present = otherDataPresent;
            } else if (privious == null) {
                console.warn('StreamMuxConfig Missing')
                this.current_syncword_offset_ = this.findNextSyncwordOffset(offset + 3 + audioMuxLengthBytes);
                gb.destroy();
                continue;
            } else {
                streamMuxConfig = privious;
            }

            let length = 0;
            while (true) {
                let tmp = gb.readByte();
                length += tmp;
                if (tmp !== 0xFF) {
                    break;
                }
            }

            let aac_data = new Uint8Array(length);
            for (let i = 0; i < length; i++) {
                aac_data[i] = gb.readByte();
            }

            aac_frame = {};
            aac_frame.audio_object_type = (streamMuxConfig.audio_object_type);
            aac_frame.sampling_freq_index = (streamMuxConfig.sampling_freq_index);
            aac_frame.sampling_frequency = MPEG4SamplingFrequencies[streamMuxConfig.sampling_freq_index];
            aac_frame.channel_config = streamMuxConfig.channel_config;
            aac_frame.other_data_present = streamMuxConfig.other_data_present;
            aac_frame.data = aac_data;

            this.current_syncword_offset_ = this.findNextSyncwordOffset(offset + 3 + audioMuxLengthBytes);
        }

        return aac_frame;
    }

    hasIncompleteData() {
        return this.has_last_incomplete_data;
    }

    getIncompleteData() {
        if (!this.has_last_incomplete_data) {
            return null;
        }

        return this.data_.subarray(this.current_syncword_offset_);
    }

}
